<script>
import countTo from 'vue-count-to';
import { Fill, Stroke, Circle, Style, Text } from 'ol/style';
import Vector from 'ol/layer/Vector';
import * as turf from '@turf/turf';
import ol from 'ol';
import Overlay from 'ol/Overlay';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import * as echarts from 'echarts';
import appConfig from 'appConfig';

var nonTextStyle = new Style({
  fill: new Fill({ color: '#052866' }),
  stroke: new Stroke({
    color: 'rgb(11,225,250)',
    width: 2,
  }),
  text: new Text({
    font: '12px Calibri,sans-serif',
    fill: new Fill({
      color: '#329FC6',
    }),
  }),
});

export default {
  name: 'MapWindowShow',
  components: {
    countTo
  }, // 地图窗口展示
  props: {
    uiOptions: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      // 总数据量
      totalData: [
        { title: '总要素量', num: 654123, unit: '亿条' },
        { title: '总服务量', num: 875415, unit: '条' }
      ],
      // 地图
      mapId: this.uniqueId(),
      layer: {
        name: 'ktw:XZQ_DSJ',
        title: '矢量',
        vendor: 'highgis',
        url: 'http://192.168.2.24:8086/hgis/ows?SERVICE=WMS&version=1.1.1&REQUEST=GetMap&FORMAT=image%2Fpng&WIDTH=800&HEIGHT=600&layers=ktw:XZQ_DSJ&srs=EPSG:4490&bbox=104.446792602539,20.9024982452393,112.056800842285,26.388500213623&styles=polygon',
        type: 'wms',
        options: {
          isFitBounds: true,
          bbox: [104.44775390625, 21.4918479919434, 112.05640411377, 26.3890419006348]
        }
      },

      mapOptions: {
        zoom: 7,
        minZoom: 7,
        maxZoom: 13,
        center: [108.4389, 24.7117],
        rotation: 0,
        projection: 'EPSG:4490',
      },
      baseLayerData: {
        name: 'ktw:XZQ_DSJ',
        title: '矢量',
        vendor: 'highgis',
        // url: 'http://192.168.2.24:8086/hgis/ows?SERVICE=WMS&version=1.1.1&REQUEST=GetMap&FORMAT=image%2Fpng&WIDTH=800&HEIGHT=600&layers=ktw:XZQ_DSJ&srs=EPSG:4490&bbox=104.446792602539,20.9024982452393,112.056800842285,26.388500213623&styles=polygon',
        type: 'wms',
        options: {
          init: 'tile',
          vertical: false,
          extend: false,
        }
      },
      map: null,
      changeFeature: null,
      // 图表
      chart: null
    };
  },
  computed: {
    prefix() {
      return this.mapId == 'map' ? '' : `${this.mapId}/`;
    },
  },
  mounted() {

  },

  methods: {
    uniqueId(length) {
      let len = length || 9;
      let chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
      let maxPos = chars.length;
      let id = '';
      for (let i = 0; i < len; i++) {
        id += chars.charAt(Math.floor(Math.random() * maxPos));
      }
      return id;
    },
    mapMounted(e) {
      this.map = e.$map;
      // this.map.on('pointermove', this.mapMoveIn);
      this.map.on('click', (e) => {
        this.$emit('on-click-map', { map: e });
      });
      this.setGeojsonLayer();
      if (this.layer.length) {
        this.$store.commit(`${this.prefix}addServiceList`, this.layer);
      }
      this.$emit('on-mounted', e);
    },
    mapMoveIn(evt) {
      const features = this.map.getFeaturesAtPixel(evt.pixel);
      if (this.map.hasFeatureAtPixel(evt.pixel)) {
        features[0].setStyle(

          function (feature) {
            return new Style({
              fill: new Fill({ color: '#0D68AA' }),
              stroke: new Stroke({
                color: '#3192B8',
                width: 2,
              }),
              text: new Text({
                font: '12px Calibri,sans-serif',
                fill: new Fill({
                  color: '#329FC6',
                }),
                text: feature.get('XZQ_DSJ'),
              }),
            });
          },
        );
      }

      if (this.changeFeature && this.changeFeature != features[0]) {
        this.changeFeature.setStyle(
          function (feature) {
            nonTextStyle.getText().setText(feature.get('XZQ_DSJ'));
            nonTextStyle.getFill().setColor('#052866');
            return nonTextStyle;
          },
        );
      }

      this.changeFeature = features[0];

    },
    async setGeojsonLayer() {
      let that = this;
      let opt = {
        service: 'WFS',
        version: '1.0.0',
        request: 'GetFeature',
        typeName: this.layer.name,
        outputFormat: 'application/json',
      };
      let uri = new URL(this.layer.url);
      const { features } = await this.$api.wfsQuery(opt, {}, uri.origin);
      features.forEach(i => {
        var options = { tolerance: 0.002, highQuality: false };
        i.geometry = turf.simplify(i.geometry, options);
      });
      var vectorLayer = new Vector({
        source: new VectorSource(),
        style: function (feature) {
          nonTextStyle.getText().setText(feature.get('XZQ_DSJ'));
          let n = feature.get('OBJECTID') / 14;
          // 颜色范围 rgb(5,200,230) ~ rgb(5,115,230)
          let colorGamut = (200 - 115) * n;
          nonTextStyle.getFill().setColor(`rgb(5,${100 + colorGamut},230)`);
          return nonTextStyle;
        },
        zIndex: -1,
        map: this.map,
      });
      vectorLayer.getSource().addFeatures(this.getJSONFeaturesToFeaturesObject(features));
      setTimeout(() => {
        features.forEach((e, i) => {
          let params = e.properties;
          this.getEcharts(params);
        });
        console.log(features);
      }, 0);
    },
    getJSONFeaturesToFeaturesObject(geoJsonFeatures = []) {
      if (Array.isArray(geoJsonFeatures)) {
        if (
          !(geoJsonFeatures.type || geoJsonFeatures.type == 'FeatureCollection')
        ) {
          let features = {
            type: 'FeatureCollection',
            features: geoJsonFeatures,
          };
          return new GeoJSON().readFeatures(features);
        }
        return new GeoJSON().readFeatures(geoJsonFeatures);
      }
      return geoJsonFeatures;
    },
    // 鼠标移入图表
    chartsMouseup(e) {
      console.log(e);
    },
    // 生成echarts图表
    getEcharts(params) {
      // echarts图表
      let option = {
        title: {
          text: params.NAME,
          bottom: 0,
          left: 'center',
          textStyle: {
            fontSize: 12,
            color: '#fff',
            fontweight: 'lighter'
          },
        },
        // aria: {
        //   enabled: true,
        //   decal: {
        //     show: true
        //   }
        // },

        // decal: {
        //   symbol: 'rect'
        // },
        tooltip: {
          trigger: 'item',
          borderColor: '#000000cc',
          backgroundColor: '#000000cc',
          textStyle: {
            color: '#ffffff',
            fontSize: 12
          },
          color: '#ffffff',
          formatter: `<div>
            <h3 style="color:rgb(199,220,100)">南宁市</h3>
            <div>栅格：225条</div>
            <div>矢量：225条</div>
            <div>档案：125条</div>
            </div>`
        },
        grid: {
          top: 10,
          bottom: 25,
          left: 2,
          right: 2
        },
        xAxis: {
          type: 'category',
          data: ['栅格', '矢量', '档案'],
          axisLine: {
            show: true,
            lineStyle: {
              color: 'rgba(167, 255, 255)',
              width: 3
            }
          },
          axisTick: {
            show: false
          },
          minorTick: {},
          axisLabel: {
            show: false
          }
        },
        yAxis: {
          type: 'value',
          show: false
        },
        series: [
          {
            type: 'pictorialBar',
            symbolSize: [10, 8],
            symbolOffset: [0, -8],
            z: 12,
            itemStyle: {
              normal: {
                color: '#ffffff66',
                borderColor: '#fff',
                borderWidth: 20
              }
            },
            data: [
              {
                value: 120,
                symbolPosition: 'end'
              },
              {
                value: 150,
                symbolPosition: 'end'
              },
              {
                value: 200,
                symbolPosition: 'end'
              }
            ]
          },
          {
            data: [
              {
                value: 120,
                itemStyle: {
                  color: 'rgb(59,237,154)'
                }
              },
              {
                value: 150,
                itemStyle: {
                  color: 'rgb(242,191,53)'
                }
              },
              {
                value: 200,
                itemStyle: {
                  color: 'rgb(130,95,247)'
                }
              }
            ],
            type: 'bar',
            barWidth: 12,
            itemStyle: {
              borderRadius: [6, 6, 0, 0]
            },
          }
        ]
      };
      let position = [(params.bbox[2] + params.bbox[0]) / 2, (params.bbox[3] + params.bbox[1]) / 2];
      let dom = document.getElementsByClassName('ol-layer')[0];
      let div = document.createElement('div');
      div.innerHTML = `<div id="chart${params.OBJECTID}" class="charts" style="position: relative;width: 60px;height: 80px;"></div>`;
      dom.appendChild(div);
      // 创建Overlay弹出层绑定DOM
      let overlay = new Overlay({
        element: document.getElementById(`chart${params.OBJECTID}`), // echarts图表DOM
        position: position,
        autoPan: {
          animation: {
            duration: 250
          }
        },
        positioning: 'center-center',
      });
      // 添加到map
      this.map.addOverlay(overlay);
      this.chart = echarts.init(document.getElementById(`chart${params.OBJECTID}`));
      this.chart.setOption(option);
    },
    // 下拉框
    selectChange(e) {
      console.log(e.target.value);
    }

  },
};
</script>

<template>
  <div class="view-map">
    <!-- 下拉框 -->
    <div class="select">
      <select
        class="select-cont"
        @change="selectChange"
      >
        <option
          value="1"
          class="select-cont-op"
          selected
        >广西壮族自治区</option>
        <option value="2">广西壮族自治区1</option>
        <option value="3">广西壮族自治区2</option>
      </select>
    </div>
    <!-- 数据总量 -->
    <div class="total">
      <div
        v-for="(item,index) in totalData"
        :key="index"
        class="factor"
      >
        <span class="title">{{ item.title }}</span>
        <span
          v-for="(e,i) in item.num.toString().split('')"
          :key="i"
          class="numbg"
        >
        </span>
        <span class="unit">{{ item.unit }}</span>
        <div class="number">
          <countTo
            :start-val="0"
            :end-val="item.num"
            :duration="1000"
            separator=""
          ></countTo>
        </div>
      </div>

    </div>
    <!-- 数据量刻度标尺 -->
    <div class="scale">
      <div>高</div>
      <div class="color"></div>
      <div>低</div>
      <div class="high">
        <span class="icon"></span>2500
      </div>
      <div class="low">
        <span class="icon"></span>0
      </div>
    </div>
    <!-- 图例 -->
    <div class="legend">
      <div><span class="legend-item"></span>栅格</div>
      <div><span class="legend-item"></span>矢量</div>
      <div><span class="legend-item"></span>档案</div>
    </div>
    <!-- 地图 -->
    <SimpleMap
      ref="map"
      :map-id="mapId"
      :options="mapOptions"
      :ui-options="uiOptions"
      :layer="layer"
      @on-mounted="mapMounted"
    >
    </SimpleMap>
  </div>
</template>

