<script>
import * as echarts from 'echarts';
import { isNull } from '../../utils/commom';
export default {
  name: 'YearDataSizeSituation', // 各年份数据量情况

  data() {
    return {
      ChartOption: null,
      echartsData: [
        { name: '2013', value: 80 }, { name: '2014', value: 120 }, { name: '2015', value: 150 }, { name: '2016', value: 250 }, { name: '2017', value: 350 }, { name: '2018', value: 450 }, { name: '2019', value: 500 }, { name: '2020', value: 550 }, { name: '2021', value: 610 }, { name: '2022', value: 50 }
      ]
    };
  },

  mounted() {
    this.getData();
    let that = this;
    window.addEventListener('resize', function () {
      that.getData();
    });
  },

  methods: {
    // 获取数据
    getData() {
      this.getEchart();
    },
    // 创建图表
    getEchart() {
      let echartsData = this.echartsData;
      let args = {
        // 直角坐标系内绘图网格
        grid: {
          top: '80%',
          bottom: '90%',
          left: '50',
          right: '50',
        },
        tooltip: {},
        xAxis: {
          data: echartsData.map(e => e.name),
          type: 'category',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.1)'
            }
          },
          axisLabel: {
            interval: 0,
            textStyle: {
              color: '#FFFFFFaa',
              fontSize: 12,
            },
            margin: 10, //刻度标签与轴线之间的距离。
          },
        },
        yAxis: {
          type: 'value',
          max: 800,
          // 坐标轴轴线
          axisLine: {
            show: true,
            lineStyle: {
              color: '#ffffff22'
            }
          },
          // 坐标轴刻度标签
          axisLabel: {
            color: '#FFFFFFaa'
          },
          // 分隔线
          splitLine: {
            show: true,
            lineStyle: {
              color: '#ffffff22'
            }
          }
        },
        dataZoom: {
          start: 0,
          type: 'inside'
        },
        series: [
          {
            data: echartsData.map(e => e.value),
            type: 'bar',
            barWidth: 14,
            // 图形样式
            itemStyle: {
              color: '#0b8ce7',
              borderRadius: 7
            }
          }
        ]
      };
      if (!isNull(this.ChartOption)) {
        echarts.dispose(this.ChartOption);
      }
      this.$nextTick(() => {
        this.ChartOption = echarts.init(this.$refs.YearDataSizecharts);
        this.ChartOption.setOption(args);
      });
    },
  },
};
</script>

<template>
  <div class="year-data-size-situation">
    <div
      ref="YearDataSizecharts"
      class="year-data-size-charts"
    ></div>
  </div>
</template>

<style lang="less" scoped>
.year-data-size-situation {
  width: 100%;
  height: 100%;
  .year-data-size-charts {
    width: 100%;
    height: 100%;
  }
}
</style>
