
<script>
import * as echarts from 'echarts';
import { isNull } from '../../utils/commom';

export default {
  name: 'SpaceBusinessSituation', // 空间业务情况

  data() {
    return {
      ChartOption: null,
      ChartOption2: null, timeTicket: null,
      echartsData: [
        { name: '现状数据量', value: '200', unit: 'TB' },
        { name: '规划数据量', value: '405', unit: 'TB' },
        { name: '管理数据量', value: '360', unit: 'TB' },
        { name: '项目数据量', value: '635', unit: 'TB' }
      ],
      chartColor: {
        top: [
          '#00fff5',
          '#ffcc00',
          '#b9b7ff',
          '#b9b7ff'],
        bottom: [
          '#43bafe',
          '#ff7800',
          '#e9a5ff',
          '#e9a5ff'],
        bottomInside: [
          '#43bafe',
          '#ff7800',
          '#e9a5ff',
          '#e9a5ff'],
        bottomOutside: [
          '#43bafe',
          '#ff7800',
          '#e9a5ff',
          '#e9a5ff'],
        pillar: [
          'rgba(0, 229, 255, 0.3)',
          'rgba(34, 224, 115, 0.3)',
          'rgba(255, 155, 98, 0.3)',
          'rgba(136, 84, 255, 0.3)'
        ],
      },
      base64List: {
        top: [
          'data:image/png;base64,',
          'data:image/png;base64,',
          'data:image/png;base64,',
          'data:image/png;base64,'
        ],
        bottom: [
          'data:image/png;base64,',
          'data:image/png;base64,',
          'data:image/png;base64,',
          'data:image/png;base64,'],
      },
    };
  },

  mounted() {
    this.getData();
    // this.setGraphic();
    let that = this;
    window.addEventListener('resize', function () {
      that.getData();
    });
  },

  methods: {
    // 获取数据
    getData() {
      // let jsonFilter = ' 1=1 ';
      // if (this.selectXzqdm) {
      //   jsonFilter = getFilterBySql(`\"xzqdm\" = \'${this.selectXzqdm}\'`);
      // }
      // api.target.getdefaulttablebyfilter({ code: this.configData.targetcode, jsonfilter: jsonFilter }).then((e) => {
      //   if (e.status != 200) {
      //     this.$Message.warning('获取数据失败');
      //     return;
      //   }
      //   this.echartsData = e.data;
      //   this.getEchart();
      // }).catch((e) => {
      //   this.$Message.warning('获取数据失败');
      // });
      this.getEchart();
    },
    // 配置图表
    getEchart() {
      let echartsData = this.echartsData;
      /*if (!isNull(this.selectXzqdm)) {
        echartsData = this.echartsData.filter(s =>
          s[this.configData.targetFilterField] === this.selectXzqdm
        );
      }*/
      /*let xData = echartsData.map(s => s.type);
      let yData = echartsData.map(s => s.imageData);*/
      let args = {
        grid: {
          top: '65%',
          bottom: '90%',
          left: '5',
          right: '5',
        },
        tooltip: {},
        xAxis: {
          data: echartsData.map(s => `${s.name}（${s.unit}）`),
          axisTick: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          axisLabel: {
            interval: 0,
            textStyle: {
              color: '#5990D9',
              fontSize: 12,
            },
            margin: 30, //刻度标签与轴线之间的距离。
          },
        },
        yAxis: {
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
          axisLine: {
            show: false,
          },
          axisLabel: {
            show: false,
          },
        },
        dataZoom: {
          start: 0,
          type: 'inside'
        },
        series: [{
          name: '',
          type: 'pictorialBar',
          // symbolSize: [40, 25],
          // symbolOffset: [0, -10],
          z: 12,
          data: echartsData.map((s, index) => {
            return {
              name: `${s.name}（${s.unit}）`,
              value: s.value,
              trueVal: parseInt(s.value / 10),
              symbolPosition: 'end',
              itemStyle: {
                normal: {
                  opacity: 0,
                  color: this.chartColor.top[index],  //圆柱顶部颜色
                },
              },
            };
          }),
          markPoint: {
            data: echartsData.map((s, index) => {
              return {
                coord: [index, s.value],
                symbolSize: [60, 25],
                symbolOffset: [0, 0],
                symbol: 'image://' + this.base64List.top[index],
                label: {
                  show: false,
                  normal: {
                    show: false,
                  },
                },
                itemStyle: {
                  opacity: 0.7,
                },
              };
            }),
          },
        }, {
          type: 'bar',
          silent: true,
          barWidth: 60,
          barGap: '-100%',
          z: 10,
          data: echartsData.map((s, index) => {
            return {
              name: s.name,
              value: s.value,
              trueVal: parseInt(s.value / 10),
              label: {
                normal: {
                  show: true,
                  position: 'inside',
                  distance: 40,
                  textStyle: {
                    color: '#ffffff', //柱子对应数值颜色
                    fontSize: 20,
                  },
                },
              },
              itemStyle: {
                normal: {
                  color: {
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    type: 'linear',
                    global: false,
                    colorStops: [{
                      offset: 0,
                      color: this.chartColor.pillar[index],
                    }, {
                      offset: 1,
                      color: this.chartColor.pillar[index], //底部渐变颜色
                    }],
                  },
                },
              },
            };
          }),
          markPoint: {
            data: echartsData.map((s, index) => {
              return {
                coord: [index, 0],
                symbolSize: [90, 40],
                symbolOffset: [0, -6],
                symbol: 'image://' + this.base64List.bottom[index],
                label: {
                  show: false,
                  normal: {
                    show: false,
                  },
                },
                itemStyle: {
                  opacity: 0.7,
                  show: false,
                },
              };
            }),
          },
        }],
      };
      if (!isNull(this.ChartOption)) {
        echarts.dispose(this.ChartOption);
      }
      this.$nextTick(() => {
        this.ChartOption = echarts.init(this.$refs.storeBar);
        this.ChartOption.setOption(args);
      });
    },
    // 气泡动画
    setGraphic() {
      let img2 = 'data:image/png;base64,';
      let args = {
        grid: {
          top: 10,
          bottom: 10,
        },
        tooltip: {},
        xAxis: [{
          gridIndex: 0,
          type: 'value',
          show: false,
          min: 0,
          max: 100,
          nameLocation: 'middle',
          nameGap: 5,
        }],
        yAxis: [{
          gridIndex: 0,
          min: 0,
          show: false,
          max: 100,
          nameLocation: 'middle',
          nameGap: 30,
        }],
        graphic: [
          {
            type: 'image',
            id: 'logo',
            left: '10%',
            bottom: '-60%',
            z: -10,
            bounding: 'raw',
            origin: [675, 5],
            style: {
              image: img2,
              width: 120,
              height: 120,
              opacity: 0.4,
            },
          },
          {
            type: 'image',
            id: 'logo1',
            right: '14%',
            bottom: '90%',
            z: -10,
            bounding: 'raw',
            origin: [275, 5],
            style: {
              image: img2,
              width: 80,
              height: 80,
              opacity: 0.4,
            },
          },
          {
            type: 'image',
            id: 'logo2',
            left: '10%',
            bottom: '-30%',
            z: -10,
            bounding: 'raw',
            origin: [875, 15],
            style: {
              image: img2,
              width: 60,
              height: 60,
              opacity: 0.4,
            },
          }, {
            type: 'image',
            id: 'logo3',
            left: '36%',
            bottom: '100%',
            z: -10,
            bounding: 'raw',
            origin: [975, 5],
            style: {
              image: img2,
              width: 40,
              height: 40,
              opacity: 0.4,
            },
          }
        ],
      };
      if (!isNull(this.ChartOption2)) {
        echarts.dispose(this.ChartOption2);
      }
      this.$nextTick(() => {
        this.ChartOption2 = echarts.init(this.$refs.store);
        this.ChartOption2.setOption(args);
      });
      let rotation = [0, 0, 0, 0];
      let that = this;
      this.timeTicket = setInterval(function () {
        that.ChartOption2.setOption({
          graphic: [
            {
              id: 'logo',
              rotation: -(rotation[0] += Math.PI / 1060) % (Math.PI * 0.2),
            },
            {
              id: 'logo1',
              rotation: (rotation[1] += Math.PI / 500) % (Math.PI * 0.3),
            },
            {
              id: 'logo2',
              rotation: -(rotation[2] += Math.PI / 500) % (Math.PI * 0.1),
            },
            {
              id: 'logo3',
              rotation: (rotation[3] += Math.PI / 600) % (Math.PI * 0.08),
            }
          ],
        });
      }, 20);
    },
  },
};
</script>
<template>
  <div class="space-business-situation">
    <div class="service">
      <div
        ref="store"
        style="height:100%;width:100%"
      >
      </div>
    </div>
    <div class="service">
      <div
        ref="storeBar"
        style="height:100%;width:100%"
      >
      </div>
    </div>
  </div>
</template>
