<script>
import appConfig from 'appConfig';
import { dateUtil } from '@ktw/kutil';
import ChartsBox from '../../components/ChartsBox.vue';
import DataOverview from './DataOverview.vue';
import SpaceBusinessSituation from './SpaceBusinessSituation.vue';
import YearDataSizeSituation from './YearDataSizeSituation.vue';
import ServiceVolume from './ServiceVolume.vue';
import CoreDataSituation from './CoreDataSituation.vue';
import DataSharingSituation from './DataSharingSituation.vue';
import MapWindowShow from './MapWindowShow.vue';
let week = ['星期天', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
export default {
  name: 'HomePage', // 首页
  components: {
    ChartsBox,
    DataOverview,
    SpaceBusinessSituation,
    YearDataSizeSituation,
    ServiceVolume,
    CoreDataSituation,
    DataSharingSituation,
    MapWindowShow
  },
  data() {
    return {
      title: appConfig.app.title,
      department: appConfig.app.department,
      nowDate: dateUtil.date.format(new Date(), 'YYYY-MM-DD'),
      nowTime: dateUtil.date.format(new Date(), 'HH:mm:ss'),
      nowWeek: week[new Date().getDay()]
    };
  },
  mounted() {
    this.getNowTime();
  },
  methods: {
    getNowTime() {
      setInterval(() => {
        this.nowDate = dateUtil.date.format(new Date(), 'YYYY-MM-DD');
        this.nowTime = dateUtil.date.format(new Date(), 'HH:mm:ss');
        this.nowWeek = week[new Date().getDay()];
      }, 500);
    },
  },
};
</script>
