      <div class="department">{{ department }}</div>
      <div class="time">
        <span class="nowTime">{{ nowTime }}</span>
        <span class="nowDate">{{ nowDate }}
          <span class="week">{{ nowWeek }}</span>
        </span>
      </div>
    </div>
    <div class="home-content">
      <Row class="home-content-row">
        <k-col class="col-left"
