        </div>
        </k-col>
        <k-col class="col-right"
          span="7"
        >
        <!-- 总服务量 -->
        <ChartsBox title="总服务量">
          <ServiceVolume></ServiceVolume>
        </ChartsBox>

        <!-- 核心数据情况 -->
        <ChartsBox title="核心数据情况">
          <CoreDataSituation></CoreDataSituation>
        </ChartsBox>

        <!-- 委办单位数据共享交换情况 -->
